.nav-background {
    z-index: 9;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 122px;
    transition: background 250ms;
    opacity: 0.8;

    &.scrolled {
        height: 61px;
        background: $color-darker;
    }
}


nav.desktop {
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 20px 40px;
    transition: background 250ms;

    .nav-inner {
        position: relative;
    }

    .nav-logo {
        max-width: 200px;
        transition: all 250ms;
    }

    .nav-items {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        margin: 0; padding: 0;

        li {
            display: inline-block;
            position: relative;

            a {
                @include font-light();
                display: inline-block;
                color: #fff;
                padding: 10px 15px;
                letter-spacing: 1px;
                border: 1px solid rgba(255,255,255,0);
                transition: all 250ms;

                &:hover, &.active {
                    border: 1px solid #fff;
                    text-decoration: none;
                }

                &:visited { text-decoration: none; }
            }

            ul {
                position: absolute;
                top: 40px;
                left: -50%;

                li {
                    a {
                        border: none;
                        background: #fff;
                        color: #000;
                        width: 150px;
                        &:hover, &.active {
                            border: none;
                        }
                    }
                }

                //width: 250px;
            }
        }
    }
}

nav.desktop.scrolled {
    padding: 10px 40px;
    //background: $color-darker;

    .nav-logo {
        max-width: 100px;
    }

    a {
        padding: 6px 10px;
        font-size: 0.8em;
    }
}


.contact-us {
    z-index: 20;
    position: fixed;
    top: 0;
    right: 0;
    background: #222;
    color: #fff;
    padding: 20px 20px 0 20px;
    @include font-regular();

    p {
        margin-bottom: 20px;
        font-size: 0.8em;
        letter-spacing: 1px;
    }

    a {
        color: #fff;
    }

    transform: translateX(100%);
    transition: transform 200ms;

    &.open,
    &:hover {
        transform: none;
    }
}


// Mobile Nav
nav.mobile {
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 20px 40px;
    transition: background 250ms;



    .nav-inner {
        position: relative;

        .open-mobile-nav {
            position: absolute;
            display: inline-block;
            top: 10px;
            right: 0px;
            color: #fff;
            font-size: 2em;
            padding: 10px 20px;
            background: #000;
        }
    }

    .nav-logo {
        margin: 7px 0 0 0;
        max-width: 80px;
        transition: all 250ms;
    }

    .nav-items {
        display: none;
        position: fixed;
        right: 10px;
        top: 10px;
        left: 10px;
        bottom: 10px;
        margin: 0; padding: 0;
        background: $color-dark;

        &.open { display: block;}

        li {
            display: block;
            position: relative;
            width: 100%;
            margin-bottom: 10px;

            a {
                @include font-light();
                display: block;
                color: #fff;
                padding: 10px 15px;
                letter-spacing: 1px;
                border: 1px solid rgba(255,255,255,0);
                transition: all 250ms;
                text-align: center;
                font-size: 0.8em;

                &:hover, &.active {
                    text-decoration: none;
                }

                &:visited { text-decoration: none; }

                &.close-mobile-nav {
                    text-align: right;
                    font-size: 1.2em;
                    padding: 3px 15px;
                }

                img {
                    width: 20px;
                }
            }
        }
    }
}

nav.mobile.scrolled {
    padding: 10px 40px;
    //background: $color-darker;

    .nav-logo {
        max-width: 30px;
    }

    .nav-inner {
        position: relative;

        .open-mobile-nav {
            position: absolute;
            display: inline-block;
            top: 2px;
            right: 0px;
            color: #fff;
            font-size: 1em;
            padding: 10px 15px;
            background: #000;
        }
    }
}
